import React, { Component } from "react";
// react component for creating dynamic tables
import ReactTable from "react-table";
import {
  Card,
  CardBody,
  CardHeader,
  CardTitle,
  Row,
  Col,
  FormGroup,
  Input,
} from "reactstrap";

import { PanelHeader } from "components";
import NotificationAlert from "react-notification-alert";
import Button from "components/CustomButton/CustomButton.jsx";
import moment from "moment";
import Loading from "../../components/Loading/Loading";

class CommentsTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      total: 0,
      postId: "",
      isSearching: false,
    };
  }

  _fetchComment = async () => {
    try {
      let result = await fetch(
        `https://fbtoolsvn.herokuapp.com/api/v1/comments?postId=${this.state.postId}`
      );
      let resJson = await result.json();
      if (resJson.status) {
        this.setState({
          total: resJson.total,
          data: resJson.data,
          isSearching: false,
        });
      } else {
        this.setState({ isSearching: false }, () =>
          this._showAlert("C li xy ra!!!", 5)
        );
      }
    } catch (error) {
      this.setState({ isSearching: false }, () =>
        this._showAlert("C li xy ra!!!", 5)
      );
    }
  };
  _randomComment = () => {
    if (this.state.data.length === 0) {
      this._showAlert("Cha c d liu!!!");
    } else {
      var item = this.state.data[
        Math.floor(Math.random() * this.state.data.length)
      ];
      this._showAlert(`${item.user.id} - ${item.user.name} - ${item.message}`);
    }
  };
  _showAlert = (message, timeDissmiss) => {
    var options = {
      place: "tr",
      message: message || "",
      type: "primary",
      icon: "now-ui-icons ui-1_bell-53",
    };
    if (timeDissmiss) {
      options = { ...options, autoDismiss: timeDissmiss || 10 };
    }
    this.refs.notificationAlert.notificationAlert({
      ...options,
    });
  };
  _renderContent = () => {
    if (this.state.isSearching) {
      return (
        <Card>
          <CardBody>
            <Loading hidetitle="false" style={{ margin: 25 }} />
          </CardBody>
        </Card>
      );
    }
    return (
      this.state.data.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle tag="h4">Tng: {this.state.total}</CardTitle>
            <Button
              md={3}
              onClick={() => this._randomComment()}
              color="primary"
            >
              Lt bnh lun ngu nhin
            </Button>
          </CardHeader>
          <CardBody>
            <ReactTable
              data={this.state.data}
              filterable
              columns={[
                {
                  Header: "Ngi dng",
                  accessor: "user",
                  Cell: ({ original }) => (
                    <a
                      href={`https://facebook.com/${original.user.id}`}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      {original.user.name}
                    </a>
                  ),
                  filterMethod: (filter, row) => {
                    if (!/\D/.test(filter.value)) {
                      return row._original.user.id.includes(filter.value);
                    } else {
                      return row._original.user.name
                        .toLowerCase()
                        .includes(filter.value.toLowerCase());
                    }
                  },
                  sortable: false,
                  width: 180,
                  maxWidth: 220,
                },
                {
                  Header: "Bnh lun",
                  accessor: "message",
                  Cell: ({ original }) => original.message,
                  filterMethod: (filter, row) => {
                    return row._original.message
                      .toLowerCase()
                      .includes(filter.value.toLowerCase());
                  },
                  sortable: false,
                },
                {
                  Header: "Thi gian",
                  id: "time",
                  Cell: ({ original }) => (
                    <a
                      href={`https://facebook.com/${original.comment_id}`}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      {moment
                        .unix(original.created_time)
                        .format("DD/MM/YYYY - HH:mm:ss")}
                    </a>
                  ),
                  filterMethod: (filter, row) => {
                    return moment
                      .unix(row._original.created_time)
                      .format("DD/MM/YYYY - HH:mm:ss")
                      .includes(filter.value);
                  },
                  width: 199,
                  resizable: false,
                  accessor: (data) => data.created_time,
                },
                {
                  Header: "Ngy cp nht",
                  id: "update_time",
                  Cell: ({ original }) =>
                    original.update_time.length > 0 ? (
                      <a
                        className="custom-href"
                        onClick={() =>
                          this._showAlert(
                            `Bnh lun trc : ${
                              original.update_time[
                                original.update_time.length - 2
                              ].message.text
                            }`,
                            10
                          )
                        }
                      >
                        {moment
                          .unix(
                            original.update_time[
                              original.update_time.length - 1
                            ].time
                          )
                          .format("DD/MM/YYYY - HH:mm:ss")}
                      </a>
                    ) : (
                      "Khng c"
                    ),
                  width: 199,
                  resizable: false,
                  filterable: false,
                  accessor: (data) => data.update_time,
                },
              ]}
              defaultSorted={[
                {
                  id: "time",
                  desc: true,
                },
              ]}
              defaultPageSize={10}
              showPaginationTop={false}
              className="-striped -highlight"
            />
          </CardBody>
        </Card>
      )
    );
  };
  render() {
    return (
      <div>
        <PanelHeader
          size="lg"
          content={
            <div className="header text-center d-flex flex-column align-items-center">
              <h2 className="title">Ly danh sch bnh lun</h2>
              <Col md={8}>
                <FormGroup>
                  <Input
                    value={this.state.postId}
                    onChange={(e) => this.setState({ postId: e.target.value })}
                    type="text"
                    placeholder="1015757618756358"
                  />
                </FormGroup>
              </Col>
              <Button
                md={3}
                onClick={() => {
                  this.setState({ isSearching: true, data: [] }, () =>
                    this._fetchComment()
                  );
                }}
                color="primary"
              >
                Tm kim
              </Button>
            </div>
          }
        />
        <div className="content">
          <Row>
            <Col xs={12} md={12}>
              {this._renderContent()}
            </Col>
          </Row>
        </div>
        <NotificationAlert ref="notificationAlert" />
      </div>
    );
  }
}

export default CommentsTable;



