import React, { Component } from "react";
import {
  Card,
  CardBody,
  CardHeader,
  CardTitle,
  Row,
  Col,
  Button,
  Label,
  FormGroup,
  Input,
} from "reactstrap";

import { PanelHeader } from "components";
export default class Homepage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      urlInput: "",
      resultData: null,
      errorMgs: null,
      isSearching: false,
    };
  }
  _seachUid = async () => {
    try {
      let result = await fetch(
        "https://fbtoolsvn.herokuapp.com/api/v1/find-id",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ uri: this.state.urlInput }),
        }
      );
      let resJson = await result.json();
      if (resJson.status) {
        this.setState({
          resultData: resJson.data,
          errorMgs: null,
          isSearching: false,
        });
      } else {
        this.setState({ errorMgs: "Li", isSearching: false });
      }
    } catch (error) {
      console.log(error.message);
      this.setState({ errorMgs: "Li", isSearching: false });
    }
  };
  _renderResult = () => {
    let result;
    const { resultData, errorMgs } = this.state;
    if (errorMgs) {
      result = errorMgs;
    } else {
      result = `ID Ngi dng: ${
        resultData && resultData.user_id
      } - ID Bi vit: ${resultData && resultData.post_id}`;
    }
    return <Label md={9}>{result}</Label>;
  };
  render() {
    return (
      <div>
        <PanelHeader size="sm" />
        <div className="content">
          <Row>
            <Col xs={12} md={8}>
              <Card>
                <CardHeader>
                  <CardTitle tag="h4">
                    Tra cu ID ngi dng v ID bi vit
                  </CardTitle>
                </CardHeader>
                <CardBody>
                  <Row>
                    <Col md={12}>
                      <Label md={12}>Lin kt bi vit</Label>
                      <Col md={12}>
                        <FormGroup>
                          <Input
                            value={this.state.urlInput}
                            onChange={(e) =>
                              this.setState({ urlInput: e.target.value })
                            }
                            type="text"
                            placeholder="https://www.facebook.com/LeHau.Official/videos/10105931018762706/"
                          />
                        </FormGroup>
                      </Col>
                      <Col md={12}>
                        <Button
                          md={3}
                          disabled={this.state.isSearching}
                          onClick={() => {
                            this.setState({ isSearching: true }, () =>
                              this._seachUid()
                            );
                          }}
                          color="primary"
                        >
                          Tm kim
                        </Button>
                        {this.state.resultData && this._renderResult()}
                      </Col>
                    </Col>
                  </Row>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      </div>
    );
  }
}



