import React, { Component } from "react";
import ReactTable from "react-table";
import {
  Card,
  CardBody,
  CardHeader,
  CardTitle,
  Row,
  Col,
  FormGroup,
  Input,
} from "reactstrap";

import { PanelHeader } from "components";
import NotificationAlert from "react-notification-alert";
import Button from "components/CustomButton/CustomButton.jsx";
import Loading from "../../components/Loading/Loading";

class SharesTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      total: 0,
      postId: "",
      isSearching: false,
    };
  }

  _fetchShares = async () => {
    try {
      let result = await fetch(
        `https://fbtoolsvn.herokuapp.com/api/v1/shares?postId=${this.state.postId}`
      );
      let resJson = await result.json();
      if (resJson.status) {
        this.setState({
          total: resJson.total,
          data: resJson.data,
          isSearching: false,
        });
      } else {
        this.setState({ isSearching: false }, () =>
          this._showAlert("C li xy ra!!!", 5)
        );
      }
    } catch (error) {
      this.setState({ isSearching: false }, () =>
        this._showAlert("C li xy ra!!!", 5)
      );
    }
  };
  _randomShares = () => {
    if (this.state.data.length === 0) {
      this._showAlert("Cha c d liu!!!");
    } else {
      var item = this.state.data[
        Math.floor(Math.random() * this.state.data.length)
      ];
      this._showAlert(`${item.user.id} - ${item.user.name}`);
    }
  };
  _showAlert = (message, timeDissmiss) => {
    var options = {
      place: "tr",
      message: message || "",
      type: "primary",
      icon: "now-ui-icons ui-1_bell-53",
    };
    if (timeDissmiss) {
      options = { ...options, autoDismiss: timeDissmiss || 10 };
    }
    this.refs.notificationAlert.notificationAlert({
      ...options,
    });
  };
  _renderContent = () => {
    if (this.state.isSearching) {
      return (
        <Card>
          <CardBody>
            <Loading hidetitle="false" style={{ margin: 25 }} />
          </CardBody>
        </Card>
      );
    }
    return (
      this.state.data.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle tag="h4">Tng: {this.state.total}</CardTitle>
            <Button md={3} onClick={() => this._randomShares()} color="primary">
              Lt chia s ngu nhin
            </Button>
          </CardHeader>
          <CardBody>
            <ReactTable
              data={this.state.data}
              filterable
              columns={[
                {
                  Header: "Ngi dng",
                  accessor: "user",
                  Cell: ({ original }) => (
                    <a
                      href={`https://facebook.com/${original.user.id}`}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      {original.user.name}
                    </a>
                  ),
                  filterMethod: (filter, row) => {
                    if (!/\D/.test(filter.value)) {
                      return row._original.user.id.includes(filter.value);
                    } else {
                      return row._original.user.name
                        .toLowerCase()
                        .includes(filter.value.toLowerCase());
                    }
                  },
                  sortable: false,
                  width: 180,
                  maxWidth: 220,
                },
                {
                  Header: "Lin kt bi ng",
                  accessor: "user",
                  Cell: ({ original }) => (
                    <a
                      href={original.post_url}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      {original.post_url}
                    </a>
                  ),
                  sortable: false,
                  filterable: false,
                },
              ]}
              defaultPageSize={10}
              showPaginationTop={false}
              className="-striped -highlight"
            />
          </CardBody>
        </Card>
      )
    );
  };
  render() {
    return (
      <div>
        <PanelHeader
          size="lg"
          content={
            <div className="header text-center d-flex flex-column align-items-center">
              <h2 className="title">Ly danh sch chia s</h2>
              <Col md={8}>
                <FormGroup>
                  <Input
                    value={this.state.postId}
                    onChange={(e) => this.setState({ postId: e.target.value })}
                    type="text"
                    placeholder="1015757618756358"
                  />
                </FormGroup>
              </Col>
              <Button
                md={3}
                onClick={() => {
                  this.setState({ isSearching: true, data: [] }, () =>
                    this._fetchShares()
                  );
                }}
                color="primary"
              >
                Tm kim
              </Button>
            </div>
          }
        />
        <div className="content">
          <Row>
            <Col xs={12} md={12}>
              {this._renderContent()}
            </Col>
          </Row>
        </div>
        <NotificationAlert ref="notificationAlert" />
      </div>
    );
  }
}

export default SharesTable;



