import Homepage from "../views/Homepage/Homepage";
import CommentsTable from "../views/Tables/CommentsTable";
import ReactionsTable from "../views/Tables/ReactionsTable";
import SharesTable from "../views/Tables/SharesTable";

var dashRoutes = [
  {
    path: "/dashboard",
    name: "Trang ch",
    icon: "design_app",
    component: Homepage,
  },
  {
    collapse: true,
    path: "/tables",
    name: "Bi vit",
    state: "openTables",
    icon: "files_single-copy-04",
    views: [
      {
        path: "/posts/reactions",
        name: "Cm xc",
        mini: "R",
        component: ReactionsTable,
      },
      {
        path: "/posts/comments",
        name: "Bnh lun",
        mini: "C",
        component: CommentsTable,
      },
      {
        path: "/posts/shares",
        name: "Chia s",
        mini: "S",
        component: SharesTable,
      },
    ],
  },
  { redirect: true, path: "/", pathTo: "/dashboard", name: "Dashboard" },
];
export default dashRoutes;



